// Calculate aggregate stats
function calculateStats(campaigns: EmailCampaign[]) {
	const sentCampaigns = campaigns.filter((c) => c.status === "sent");
	const totalSent = sentCampaigns.reduce((sum, c) => sum + c.sentCount, 0);
	const totalDelivered = sentCampaigns.reduce((sum, c) => sum + c.deliveredCount, 0);
	const totalOpens = sentCampaigns.reduce((sum, c) => sum + c.uniqueOpens, 0);
	const totalClicks = sentCampaigns.reduce((sum, c) => sum + c.uniqueClicks, 0);
	const totalRevenue = sentCampaigns.reduce((sum, c) => sum + c.revenueAttributed, 0);

	return {
		totalCampaigns: campaigns.length,
		activeCampaigns: campaigns.filter((c) => c.status === "sending" || c.status === "scheduled").length,
		totalSent,
		deliveryRate: totalSent > 0 ? ((totalDelivered / totalSent) * 100).toFixed(1) : "0",
		openRate: totalDelivered > 0 ? ((totalOpens / totalDelivered) * 100).toFixed(1) : "0",
		clickRate: totalDelivered > 0 ? ((totalClicks / totalDelivered) * 100).toFixed(1) : "0",
		totalRevenue,
	};
}

